.contact-menu {
    width: 45rem;
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    align-items: center;
    text-align: center;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.6); /* Semi-transparent dark background */
    border-radius: 1rem;
    color: white; /* Ensure text is visible */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add some shadow for depth */
}

.contact-menu h2 {
    margin-bottom: 1rem;
    font-size: 2rem;
    color: var(--primary-color); /* Adjust the color for better contrast */
}

.contact-menu p {
    margin: 0.5rem 0;
    font-size: 1.2rem;
}

.contact-menu a {
    color: var(--secondary-color); /* Use a color that stands out */
    text-decoration: none;
    font-weight: bold;
}

.contact-menu a:hover {
    text-decoration: underline;
}

/* Media query for screens 1100px or less */
@media (max-width: 1100px) {
    .contact-menu {
        width: 28rem;
        top: 20%;
    }

    .contact-menu h2 {
        font-size: 1.5rem;
    }

    .contact-menu p {
        font-size: 1rem;
    }
}

/* Media query for screens 700px or less */
@media (max-width: 700px) {
    .contact-menu {
        width: 20rem;
        top: 10%;
        padding: 0.5rem;
    }

    .contact-menu h2 {
        font-size: 1.2rem;
    }

    .contact-menu p {
        font-size: 0.9rem;
    }
}
