.project-menu {
    width: 45rem;
    position: fixed;
    display: flex;
    top: 15%;
    left: 50%;
    transform: translateX(-50%);
    flex-direction: column;
    align-items: center;
}

.project-item-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 5rem;
    align-items: center;
}

.project-item {
    height: 3.75rem;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    white-space: nowrap;
}

.project-item.activeProject .title {
    font-size: 1.5rem;
}

.activeProject {
    border-radius: var(--radius-left);
    cursor: default;
}

.project-item.activeProject:nth-child(1) {
    background: linear-gradient(270deg, #ffffff00, var(--purple) 70%);
}
.project-item.activeProject:nth-child(2) {
    background: linear-gradient(270deg, #ffffff00, var(--blue) 70%);
}
.project-item.activeProject:nth-child(3) {
    background: linear-gradient(270deg, #ffffff00, var(--cyan) 70%);
}

.project-sub-container {
    width: 100%;
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.375rem;
}
.project-sub-container h3 {
    position: relative;
    margin: 0;
    text-align: center;
}
.project-sub-container p {
    font-size: 0.875rem;
    text-align: center;
}

.link-container a {
    color: white;
    text-decoration: none;
    margin-top: 1rem;
    width: 50%;
    height: 2.75rem;
    text-align: center;
}

.link-container {
    display: flex;
    justify-content: space-around;
    width: 100%;
}

@media (max-width: 1100px) {
    .project-menu {
        width: 28rem;
        top: 20%;
        transform: translateX(-50%);
    }
    .project-item {
        height: 2rem;
        width: 100%;
    }
    .project-item.activeProject .title {
        font-size: 1rem;
    }
    .title {
        font-size: 0.8rem;
    }
}

@media (max-width: 700px) {
    .project-menu {
        width: 20rem;
        top: 10%;
        transform: translateX(-50%);
        align-items: center;
    }
    .project-menu p {
        font-size: 0.7rem;
    }
    .project-sub-container {
        margin-top: 1rem;
        width: 90%;
    }
    .link-container a {
        margin-top: 0.5rem;
        width: 50%;
        height: 2rem;
    }
    .project-items-container {
        margin-top: 0;
        width: 100%;
    }
}
